const width = window.innerWidth;
export function isDOM(item) {
  // HTMLELementHTMLElementDOMODM
  return typeof HTMLElement === 'function'
    ? item instanceof HTMLElement
    : item &&
        typeof item === 'object' &&
        item.nodeType === 1 &&
        typeof item.nodeName === 'string';
}


export function isCollisionWithRect(x1, y1, w1, h1, x2, y2, w2, h2) {
  if (x1 >= x2 && x1 >= x2 + w2) {
    return false;
  } else if (x1 <= x2 && x1 + w1 <= x2) {
    return false;
  } else if (y1 >= y2 && y1 >= y2 + h2) {
    return false;
  } else if (y1 <= y2 && y1 + h1 <= y2) {
    return false;
  }
  return true;
}

export function px2vw(px) {
  return `${(px * 100) / (750)}vw`;
}

export function delay(time) {
  return new Promise((resolve) => {
    setTimeout(() => resolve(), time);
  });
}

/**
 * 
 * @param {number}} weight  0 - 10
 */
export const random = (weight = 5) => {
  const r = Array.from({ length: 10 }).map((_, index) => {
    if (index < weight) {
      return 1;
    } else {
      return 0;
    }
  });

  return r[Math.floor(Math.random() * 10)];
};

/**
 * 
 * @param {number}
 */
export const TreasureBoxShowTime = (gameTime, treasureBoxCycle) => {
  const max = gameTime / 2 + 1;
  const min = gameTime / 2 - 1;
  let randomTime = Math.random() * (max - min) + min;
  if ((gameTime - randomTime) < treasureBoxCycle) {
    randomTime = gameTime - treasureBoxCycle;
  }
  return Math.floor(randomTime);
};
