import Tiny from 'tiny';
;
import BeanManger from './beanManager';
import Pacman from './pacman';
import TreasureBox from './treasureBox';
import Config from '../config';
import { PacmanLottie, EffectLottie, GameUI, TreasureBoxLottie, PacmanUI, TreasureBoxUI } from '../ui';
import { isCollisionWithRect } from '../libs/utils';
// 
const MIN_DISTANCE = 1;
// 
const DIRECTION = {
    LEFT: -1,
    STOP: 0,
    RIGHT: 1,
};
export default class Scene extends Tiny.Container {
    get averageFPS() {
        const sum = this.beanManager.fpsList.reduce((total, num) => {
            return total + num;
        });
        return Math.floor(sum / this.beanManager.fpsList.length);
    }
    get curScore() {
        return this.score;
    }
    get pacmanPosition() {
        return this.pacman && this.pacman.currentPosition;
    }
    constructor(params) {
        super(params);
        this.config = params;
        this.resources = params.resources;
        this.dropList = params.dropList;
        this.gameChance = params.gameChance;
        this.hasTreasureBox = params.hasTreasureBox;
        this.pea = params.pea;
        this.app = params.app;
        this.onFinish = params.onFinish || function () { };
        this.onScore = params.onScore || function () { };
        this.score = 0;
        this.effects = [];
        this.init();
        this.bind();
    }
    init() {
        // 
        this.direction = DIRECTION.STOP;
        // /
        this.setEventEnabled(true);
        // 
        this.hitArea = new Tiny.Rectangle(0, 0, Config.AppConfig.renderSize.width, Config.AppConfig.renderSize.height);
        // 
        this.touching = false;
        // 
        this.paused = false;
        // 
        this.stopped = true;
        // 
        // const { beanImage } = this.config.resources;
        const beanManager = new BeanManger({
            resources: this.resources,
            dropList: this.dropList,
            pea: this.pea,
            app: this.app,
            onPositionChange: (bean) => {
                this.paused || this.checkCollision(bean);
            },
            onLastOneFinish: () => {
                this.onFinish();
            },
        });
        this.addChild(beanManager);
        this.beanManager = beanManager;
        // 
        this.pacman = new Pacman();
        this.addChild(this.pacman);
        // 
        this.treasureBox = new TreasureBox({
            onUpdate: ({ x, y }) => {
                this.onTreasureBoxLottieUpdate(x, y);
            },
            onComplete: () => {
                this.onTreasureBoxLottieComplete();
            },
        });
        this.addChild(this.treasureBox);
        this.initUI();
    }
    initUI() {
        const { GameConfig } = Config;
        const uiDom = document.createElement('div');
        uiDom.className = 'BeanRainUI';
        // ui
        uiDom.style.setProperty('pointer-events', 'none');
        uiDom.style.setProperty('width', '100vw');
        uiDom.style.setProperty('height', '100vh');
        uiDom.style.setProperty('overflow', 'hidden');
        // uiDom.style.setProperty('transform', `translateY(${px2vw(Config.AppConfig.navBarHeight)})`);
        this.app.view.parentNode.appendChild(uiDom);
        const { pacmanLottie: pLottie, effectLottie: eLottie, treasureBox: tLottie } = this.resources;
        const { x, y } = this.pacman.currentPosition;
        // canvas
        const roleContainer = document.createElement('div');
        roleContainer.style.setProperty('pointer-events', 'none');
        roleContainer.style.setProperty('width', '100vw');
        roleContainer.style.setProperty('height', '100vh');
        roleContainer.style.setProperty('overflow', 'hidden');
        roleContainer.style.setProperty('position', 'absolute');
        this.app.view.parentNode.insertBefore(roleContainer, this.app.view);
        // apng
        if (GameConfig.pacmanLottieToApng) {
            // apng
            this.pacmanLottie = new PacmanUI({
                bdbox: roleContainer,
                lottieData: pLottie,
            });
        }
        else {
            // dom
            this.pacmanLottie = new PacmanLottie({
                bdbox: roleContainer,
                lottieData: pLottie,
            });
        }
        this.pacmanLottie.syncPosition(x, y);
        // 
        this.effects = Array.from({ length: Config.GameConfig.maxEffectAmount }).map(() => new EffectLottie({
            bdbox: uiDom,
            lottieData: eLottie,
            onEffectComplete: (effect) => {
                this.onEffectComplete(effect);
            },
        }));
        // 
        this.gameUI = new GameUI({
            bdbox: uiDom,
            gameChance: this.gameChance,
            hasTreasureBox: this.hasTreasureBox,
            onCountDownComplete: () => {
                this.beanManager.stopFall();
            },
            onShowTreasureBox: () => {
                // 
                this.treasureBox.startMove();
                this.treasureBoxLottie.play();
            },
        });
        // 
        const { x: treasureBoxX, y: treasureBoxY } = this.treasureBox.currentPosition;
        if (GameConfig.treasureBoxLottieToApng) {
            this.treasureBoxLottie = new TreasureBoxUI({
                bdbox: uiDom,
                lottieData: tLottie,
            });
        }
        else {
            this.treasureBoxLottie = new TreasureBoxLottie({
                bdbox: uiDom,
                lottieData: tLottie,
            });
        }
        this.treasureBoxLottie.syncPosition(treasureBoxX / 2, treasureBoxY / 2);
    }
    bind() {
        const { dpi } = Config.AppConfig;
        let _lastTouchX = null;
        let _startPoint = null;
        // 
        this.on('touchstart', (event) => {
            if (this.paused) {
                return;
            }
            this.touching = true;
            _lastTouchX = event.data.global.x;
            // 
            _startPoint = {
                x: event.data.global.x,
                y: event.data.global.y,
            };
        });
        this.on('touchmove', (event) => {
            const { x, y } = event.data.global;
            // 
            if (!this.paused && this.touching) {
                if (x > _lastTouchX &&
                    this.direction !== DIRECTION.RIGHT &&
                    Math.abs(x - _lastTouchX) >= MIN_DISTANCE) {
                    // 
                    this.direction = DIRECTION.RIGHT;
                    this.pacmanLottie.turnRight();
                }
                else if (x < _lastTouchX &&
                    this.direction !== DIRECTION.LEFT &&
                    Math.abs(x - _lastTouchX) >= MIN_DISTANCE) {
                    // 
                    this.direction = DIRECTION.LEFT;
                    this.pacmanLottie.turnLeft();
                }
                // this.pacmanLottie.move();
                _lastTouchX = event.data.global.x;
                // 
                this.pacman.moveTo((x - _startPoint.x) * dpi, (y - _startPoint.y) * dpi);
                const _x = this.pacman.currentPosition.x;
                const _y = this.pacman.currentPosition.y;
                // 
                this.pacmanLottie.syncPosition(_x, _y);
            }
        });
        this.on('touchend', () => {
            _lastTouchX = null;
            if (this.direction !== DIRECTION.STOP)
                this.direction = DIRECTION.STOP;
            this.touching = false;
            _startPoint = null;
            this.pacman.moveEnd();
        });
        this.on('touchendoutside', () => {
            _lastTouchX = null;
            if (this.direction !== DIRECTION.STOP)
                this.direction = DIRECTION.STOP;
            this.touching = false;
            _startPoint = null;
            this.pacman.moveEnd();
        });
    }
    run() {
        this.paused = false;
        this.stopped = false;
        this.gameUI.startCount();
        this.beanManager.startUp();
    }
    destroy() {
        super.destroy.call(this);
        this.pacmanLottie.destroy();
        this.effects.forEach((item) => item.destroy());
        this.gameUI.destroy();
    }
    /**
     * 
     * @param {Bean}} bean 
     */
    checkCollision(bean) {
        if (bean.position.y > this.pacman.bounds.y) {
            const { x, y, width, height } = this.pacman.bounds;
            // 
            const isCollision = isCollisionWithRect(bean.position.x, bean.position.y, bean.width, bean.height, x, y, width, height);
            // 
            if (isCollision) {
                // console.log('>>>>> this.pacmanLottie.position', this.pacmanLottie.position);
                // 
                // this.pacmanLottie.eat(bean);
                // 
                const { RewardType } = Config;
                const rewardType = bean._rewardType || RewardType.PEA;
                const score = (bean === null || bean === void 0 ? void 0 : bean._score) || 0;
                // 
                const effect = this.effects.shift();
                effect && effect.show({
                    x: this.pacmanLottie.position.x,
                    y: this.pacmanLottie.position.y,
                    rewardType,
                    score,
                });
                // 
                if (rewardType === RewardType.BOMB) {
                    this.score -= Number(score);
                    this.score = this.score > 0 ? this.score : 0;
                }
                else {
                    this.score += Number(score);
                }
                // 
                this.beanManager.onBeanComplete(bean);
                this.gameUI.setScore(this.score);
            }
        }
    }
    onEffectComplete(effect) {
        this.effects.push(effect);
    }
    pause() {
        this.paused = true;
        this.beanManager.pause();
        this.pacmanLottie.pause();
        this.treasureBoxLottie.pause();
        this.gameUI.pause();
        this.treasureBox.pause();
    }
    stop() {
        const { dpi } = Config.AppConfig;
        this.paused = true;
        this.stopped = true;
        this.beanManager.resetPosition();
        this.pacmanLottie.pause();
        this.treasureBoxLottie.pause();
        this.pacman.reset();
        this.treasureBox.reset();
        const _x = this.pacman.currentPosition.x;
        const _y = this.pacman.currentPosition.y;
        this.pacmanLottie.syncPosition(_x / dpi, _y / dpi).mirror();
        // treasureBoxLottieLottie
        const _treasureBoxX = this.treasureBox.currentPosition.x;
        const _treasureBoxY = this.treasureBox.currentPosition.y;
        this.treasureBoxLottie.syncPosition(_treasureBoxX / dpi, _treasureBoxY / dpi);
        this.gameUI.reset().hide();
    }
    resume() {
        if (!this.stopped) {
            this.gameUI.resume();
        }
        this.paused = false;
        this.stopped = false;
        this.beanManager.resume();
        this.pacmanLottie.resume();
        this.treasureBoxLottie.resume();
        this.treasureBox.resume();
    }
    restart() {
        this.score = 0;
        this.paused = false;
        this.stopped = false;
        this.touching = false;
        this.gameUI.restart();
        this.beanManager.restart();
        this.pacmanLottie.resume();
        this.treasureBoxLottie.resume();
    }
    /**
     * ,init,initUI
     */
    update(config) {
        this.resources = config.resources;
        this.dropList = config.dropList;
        this.gameChance = config.gameChance;
        this.hasTreasureBox = config.hasTreasureBox;
        this.beanManager.update(config);
        this.gameUI.update(config);
    }
    /**
     * , lottie
     */
    onTreasureBoxLottieUpdate(x, y) {
        this.treasureBoxLottie.syncPosition(x / 2, y / 2);
    }
    /**
     * lottie
     */
    onTreasureBoxLottieComplete() {
        this.treasureBoxLottie.playFlashing();
    }
}
