import Tiny from 'tiny';
import Config from '../config';
console.log('>>>>> Tiny', Tiny);
export default class Bean extends Tiny.Sprite {
    constructor(params, config) {
        super(params, config);
        this.init(config);
        this.reset();
    }
    _getUuid() {
        const s = [];
        const hexDigits = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ';
        for (let i = 0; i < 36; i++) {
            s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
        }
        s[14] = '4';
        s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);
        // eslint-disable-next-line no-multi-assign
        s[8] = s[13] = s[18] = s[23] = '-';
        const uuid = s.join('');
        return uuid;
    }
    init(config) {
        this.config = config;
        this._rewardType = config.beanProp.rewardType;
        this._score = config.beanProp.score;
        this.resources = config.resources;
        this.pea = config.pea;
        this.id = this._getUuid();
        // 
        this.onComplete = config.onComplete || function () { };
        // 
        this.onUpdate = config.onUpdate || function () { };
        // 
        this.beanScale = 1;
        // 
        this.running = false;
        // 
        this.beanSpeed = config.beanSpeed || 1;
        // 
        this.setAnchor(0.5);
        // 
        this.setScale(this.beanScale);
        // 
        this.setPosition(-1000, 0);
    }
    /**
     * 
     */
    reset() {
        const { beanSizeRange } = Config.GameConfig;
        this.running = false;
        // 
        this.beanScale = (Math.random() * (1 - beanSizeRange) + beanSizeRange).toFixed(2);
        this.setScale(this.beanScale);
        // this.beanSpeed = (Math.random() * 1.5).toFixed(2);
        // 
        this.setPosition(-1000, 0);
        // 
        this.removeActions();
        return this;
    }
    start() {
        const { beanCycle } = Config.GameConfig;
        const self = this;
        // 
        if (this.action) {
            this.action.stop();
            this.action = null;
        }
        // 
        const action = Tiny.MoveTo((beanCycle * 1000) / this.beanSpeed, Tiny.point(this.position.x, Config.AppConfig.renderSize.height));
        action.setEasing(Tiny.TWEEN.Easing.Quadratic.In);
        action.onComplete = function (tween, object) {
            self.onComplete(self);
            this._onComplete.call(this, tween, object); //eslint-disable-line
        };
        action.onUpdate = function (tween) {
            if (self.running) {
                self.setPosition(tween.x, tween.y);
                self.onUpdate(self);
                // this._onUpdate.call(this, tween, object);
            }
        };
        this.runAction(action);
        this.action = action;
        this.running = true;
        return this;
    }
    pause() {
        this.action && this.action.pause();
    }
    resume() {
        this.action && this.action.resume();
    }
    changeBean() {
        const { texture } = this.resources.beanImage;
        this.texture = texture;
        this._rewardType = Config.RewardType.PEA;
        this._score = this.pea.score;
    }
}
