import { isDOM } from '../../libs/utils';
import './index.css';
import Config from '../../config';
import lottie from 'lottie-web';
// <div class="activity-tip" style="background-image:url(${data.tipBg});display:${data.showTip ? 'block' : 'none'};">
const tpl = (data) => `
  <div class="game-count-down"></div>
  <div class="activity-tip" style="background-image:display:${data.showTip ? 'block' : 'none'};">
    <em style="background-image:url(${data.hand});"></em>
  </div>
`;
export default class CountDown {
    constructor(config) {
        this.config = config;
        // 
        this.bdbox = isDOM(config.bdbox)
            ? config.bdbox
            : document.querySelector(config.bdbox) || document.body;
        this.onStartGame = config.onStartGame || function () { };
        const dom = document.createElement('div');
        dom.id = 'BeanRainCountDown';
        this.bdbox.appendChild(dom);
        dom.innerHTML = tpl({
            // showTip: !window.localStorage.getItem('BeanRanGameTip'),
            showTip: true,
            tipBg: Config.Resources.gameTipBg,
            hand: Config.Resources.gameTipHand,
        });
        this.dom = dom;
        // 
        dom.addEventListener('touchmove', (e) => {
            e.preventDefault();
            e.stopPropagation();
            return false;
        });
        const tipNode = dom.querySelector('.activity-tip');
        tipNode === null || tipNode === void 0 ? void 0 : tipNode.addEventListener('touchstart', (e) => {
            const target = e === null || e === void 0 ? void 0 : e.currentTarget;
            if (target) {
                target.style.setProperty('display', 'none');
            }
            // window.localStorage.setItem('BeanRanGameTip', 'true');
        });
        this.initLottie();
        this.bind();
    }
    /**
     * lottie
     */
    initLottie() {
        const { lottieData } = this.config;
        // lottie
        this.lottie = lottie.loadAnimation({
            container: this.dom.children[0],
            renderer: 'svg',
            loop: false,
            autoplay: false,
            animationData: lottieData.data,
        });
    }
    bind() {
        // 
        this.lottie.addEventListener('complete', () => {
            this.dom.classList.add('hidden');
            this.dom.classList.remove('animate');
            this.onStartGame();
        });
    }
    show() {
        this.dom.classList.remove('hidden');
        this.dom.classList.add('animate');
        this.lottie.goToAndPlay(0);
    }
    /**
     * 
     */
    destroy() {
        this.lottie.destroy();
        this.bdbox && this.bdbox.removeChild && this.bdbox.removeChild(this.dom);
    }
}
