___CSS_LOADER_EXPORT___.push([module.id, `#TreasureBoxLottie {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  display: none;
}

#TreasureBoxLottie .treasure-box {
  pointer-events: none;
  transform: translate(-50%, -50%);
}

#TreasureBoxLottie .treasure-box-img {
  width: 35.2vw;
  height: 35.2vw;
}

#TreasureBoxLottie .treasure-box-img--opacity {
  animation: 1s linear 1 both treasure-box-img--opacity;
}

#TreasureBoxLottie .treasure-box-img--rotate {
  animation: 1s linear 1 both treasure-box-img--rotate;
}

#TreasureBoxLottie .treasure-box-img--scale {
  animation: 1s linear 1 both treasure-box-img--scale;
}



@keyframes treasure-box-img--opacity {
  0% {
    opacity: 1;
  }

  75% {
    opacity: 1; animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  100% {
    opacity: 0; animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }
}

@keyframes treasure-box-img--rotate {
  0% {
    transform: rotate(0);
  }

  33% {
    transform: rotate(-32deg); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  100% {
    transform: rotate(32deg); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }
}

@keyframes treasure-box-img--scale {
  0% {
    transform: scale(1);
  }

  17% {
    transform: scale(0.6); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  50% {
    transform: scale(1.6); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  100% {
    transform: scale(0.7); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }
}
