import Tiny from 'tiny';
;
import { delay } from './libs/utils';
import Config from './config';
import Scene from './core/scene';
import Loader from './core/loader';
import { CountDown } from './ui';
console.log('>>>>> Tiny', Tiny);
class Game {
    // 
    get isStop() {
        return this._stop;
    }
    // 
    get gameScore() {
        return this.scene && this.scene.curScore;
    }
    // 
    set pea(pea) {
        this._pea = pea;
    }
    constructor(config) {
        const { bdbox, dropList, gameChance, hasTreasureBox, onGameStart = () => { } } = config;
        // 
        this.ee = new Tiny.EventEmitter();
        if (bdbox && dropList && dropList.length && gameChance) {
            this.bdbox = bdbox || document.body;
            this.dropList = dropList;
            // 
            this.gameChance = gameChance;
            this.hasTreasureBox = hasTreasureBox;
            this.onGameStart = onGameStart;
            // 
            this.loader = new Loader(dropList);
            // 
            this.running = false;
            // 
            this._stop = false;
            // 
            this.initialized = false;
            // 
            this._hasTreasureBox = false;
            // 
            this._treasureBox = {};
            // 
            this._pea = {};
            this.bindEvent();
            this.sync();
        }
        else {
            this.exit();
        }
        return this;
        // this.sync();
    }
    render(resources) {
        this.resources = resources;
        console.log('>>>>> 13123', 123123);
        // App
        const app = new Tiny.Application(Object.assign(Config.AppConfig, {
            canvasId: this.bdbox,
        }));
        this.app = app;
        // 
        const { countDownLottie } = resources;
        this.countDown = new CountDown({
            bdbox: app.view.parentNode,
            lottieData: countDownLottie,
            onStartGame: () => {
                this.onGameStart();
                // this.countDown.destroy();
                // 
                this.isStop ? this.restart() : this.start();
            },
        });
        // 
        const scene = new Scene({
            hasTreasureBox: this.hasTreasureBox,
            resources,
            dropList: this.dropList,
            gameChance: this.gameChance,
            pea: this._pea,
            app: this.app,
            onFinish: () => {
                // 
                // 
                this.pause();
                this._stop = true;
                this.ee.emit('BeanRain:GameFinish', this.scene.curScore, this.scene.averageFPS);
            },
        });
        this.app.run(scene);
        this.scene = scene;
        this.initialized = true;
        this.ee.emit('BeanRain:GameRenderer');
    }
    bindEvent() {
        const instance = this;
        this.loader.event.on('BeanRain:Loaded', (res) => {
            const { resource, } = res;
            instance.render(resource);
        });
        this.loader.event.on('BeanRain:LoadError', ({ error }) => {
            instance.ee.emit('BeanRain:LoadError', error);
        });
    }
    sync() {
        this.loader.load();
    }
    update(resources) {
        // 
        if (this.initialized) {
            // sceneInitInitUI
            this.scene.update({
                resources,
                dropList: this.dropList,
                gameChance: this.gameChance,
                hasTreasureBox: this._hasTreasureBox,
                pea: this._pea,
            });
            this.ee.emit('BeanRain:GameRenderer');
            this.initialized = true;
        }
    }
    exit() {
        this.ee.emit('BeanRain:LoadError');
    }
    destroy() {
        this.app.destroy(true);
        this.scene.destroy();
        this.countDown.destroy();
        this.running = false;
        this.initialized = false;
        this._stop = false;
    }
    start() {
        this.running = true;
        if (this.app.isPaused()) {
            this.app.resume();
        }
        this.scene.run();
    }
    pause() {
        if (this._stop || !this.initialized) {
            return;
        }
        // this.running = false;
        // this.scene.pause();
        // this.app.pause();
        /**
         * 
         */
        delay(100).then(() => {
            this.running = false;
            this.scene.pause();
            this.app.pause();
        });
    }
    /**
     * 
     */
    stop() {
        this.focusStop();
    }
    /**
     * 
     */
    focusStop() {
        this._stop = true;
        this.running = false;
        this.app.resume();
        this.scene.stop();
        setTimeout(() => this.app.pause(), 100);
    }
    /**
     * 
     */
    resume() {
        if (this._stop || !this.initialized) {
            return;
        }
        this.running = true;
        this.app.resume();
        this.scene.resume();
    }
    restart() {
        this._stop = false;
        this.running = true;
        this.app.resume();
        this.scene.restart();
    }
    /**
     * 
     */
    startCountDown() {
        delay(500).then(() => this.countDown.show());
        this.scene.resume();
    }
}
export default Game;
