import './index.css';
import lottie from 'lottie-web';
import { isDOM, px2vw } from '../../libs/utils';
import Config from '../../config';
const EffectSegments = {
    Good: [0, 15],
    Nice: [15, 30],
    BOMB: [30, 45],
};
const tpl = (data) => `
  <div class="text-effect" style="width:${data.width};height:${data.height};">
  </div>
`;
export default class Effect {
    constructor(config) {
        const { lottieData } = config;
        const { w, h } = lottieData.data;
        // console.log('>>>>> effect', w, h );
        this.width = w;
        this.height = h;
        this.running = false;
        this.DOMLoaded = false;
        this.config = config;
        this.onEffectComplete = config.onEffectComplete || function () { };
        // 
        this.type = '';
        this.bdbox = isDOM(config.bdbox)
            ? config.bdbox
            : document.querySelector(config.bdbox) || document.body;
        this.initDom();
        return this;
    }
    initDom() {
        const dom = document.createElement('div');
        dom.className = 'BeanRainEffect';
        this.bdbox.appendChild(dom);
        const data = {
            width: px2vw(this.width),
            height: px2vw(this.height),
        };
        dom.innerHTML = tpl(data);
        this.dom = dom;
        const { lottieData } = this.config;
        this.lottie = lottie.loadAnimation({
            container: this.dom.children[0],
            renderer: 'svg',
            loop: false,
            autoplay: false,
            animationData: lottieData.data,
        });
        this.lottie.addEventListener('DOMLoaded', () => {
            this.DOMLoaded = true;
        });
        this.lottie.addEventListener('complete', () => {
            this.running = false;
            this.onEffectComplete(this);
        });
        this.lottie.addEventListener('segmentStart', () => {
            this.running = true;
        });
        // this.lottie = lottie.loadAnimation({
        //   container: this.dom.children[0],
        //   renderer: 'svg',
        //   loop: false,
        //   autoplay: false,
        //   animationData: lottieData.data,
        // });
    }
    show({ x, y, rewardType, score }) {
        // 
        const { RewardType } = Config;
        const isBomb = rewardType === RewardType.BOMB;
        if (!this.running && this.DOMLoaded) {
            this.dom.style.setProperty('transform', `translate3d(${px2vw(x / Config.AppConfig.renderSize.width * 750)},${px2vw(y / Config.AppConfig.renderSize.width * 750 - 200)}, 0)`);
            const curScore = isBomb ? `-${score}` : `+${score}`;
            if (rewardType === RewardType.PEA) {
                this.peaScoreGoodDom = this.dom.querySelector('#pea_score_good');
                this.peaScoreGoodDomTspan = this.peaScoreGoodDom.querySelector('tspan');
                this.peaScoreGoodDomTspan.innerHTML = curScore;
                this.peaScoreGoodDomTspan.style.fontFamily = 'PingFangSC-Semibold;';
                this.peaScoreNiceDom = this.dom.querySelector('#pea_score_nice');
                this.peaScoreNiceDomTspan = this.peaScoreNiceDom.querySelector('tspan');
                this.peaScoreNiceDomTspan.innerHTML = curScore;
                this.peaScoreNiceDomTspan.style.fontFamily = 'PingFangSC-Semibold;';
            }
            else if (rewardType === RewardType.BOMB) {
                this.peaScoreBoomDom = this.dom.querySelector('#pea_score_boom');
                this.peaScoreBoomDomTspan = this.peaScoreBoomDom.querySelector('tspan');
                this.peaScoreBoomDomTspan.innerHTML = curScore;
                this.peaScoreBoomDomTspan.style.fontFamily = 'PingFangSC-Semibold;';
            }
            else {
                this.peaScoreNiceDom = this.dom.querySelector('#pea_score_nice');
                this.peaScoreNiceDomTspan = this.peaScoreNiceDom.querySelector('tspan');
                this.peaScoreNiceDomTspan.innerHTML = curScore;
                this.peaScoreNiceDomTspan.style.fontFamily = 'PingFangSC-Semibold;';
            }
            this.lottie.playSegments(this._actionType(rewardType), true);
        }
    }
    _actionType(rewardType) {
        // 
        const { RewardType } = Config;
        if (rewardType === RewardType.BOMB) {
            return EffectSegments.BOMB;
        }
        else {
            return 0 ? EffectSegments.Good : EffectSegments.Nice;
        }
    }
    destroy() {
        this.lottie.destroy();
        this.bdbox && this.bdbox.removeChild && this.bdbox.removeChild(this.dom);
    }
}
