___CSS_LOADER_EXPORT___.push([module.id, `.BeanRainEffect {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  transform: translate(-50%, -50%);
}

.BeanRainEffect .text-effect {
  pointer-events: none;
  transform: translate(-50%, -50%);
  position: relative;
}

.BeanRainEffect .text-effect-score {
  font-weight: bold;
  position: absolute;
  bottom: 23.2vw;
  right: 4vw;
  font-size: 8vw;
  opacity: 0;
}


.BeanRainEffect .text-effect-score--add {
  color: #fff880;
}

.BeanRainEffect .text-effect-score--plus {
  color: #ff553e;
}

.BeanRainEffect .text-effect-score--scale {
  animation: 400ms linear 1 both 'score_scale';
}

.BeanRainEffect .text-effect-score--opacity {
  animation: 400ms linear 1 both 'score_opacity';
}

@keyframes score_scale {
  0% {
    transform: scale(0, 0);
  }

  40% {
    transform: scale(1.3, 1.3); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  90% {
    transform: scale(1, 1); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  100% {
    transform: scale(0, 0); animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }
}


@keyframes score_opacity {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1; animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  90% {
    opacity: 1; animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }

  100% {
    opacity: 0; animation-timing-function: cubic-bezier(0.33, 0, 0.67, 1);
  }
}
