import './index.css';
import Config from '../../config';
import { isDOM, px2vw } from '../../libs/utils';
const tpl = (data) => `
  <div class="pacman-box" style="width:${data.width};height:${data.height};">
    <img class="pacman-box-img" src=${data.src}></img>
  </div>`;
export default class PacmanUI {
    get position() {
        return { x: this._x, y: this._y };
    }
    constructor(config) {
        const { lottieData } = config;
        const { w, h } = lottieData.data;
        this.width = w;
        this.height = h;
        // 
        this._x = 0;
        this._y = 0;
        // 
        this.bdbox = isDOM(config.bdbox)
            ? config.bdbox
            : document.querySelector(config.bdbox) || document.body;
        this.config = config;
        this.initDom();
        // this.initRole();
    }
    /**
     * dom
     */
    initDom() {
        const dom = document.createElement('div');
        dom.id = 'PacmanLottie';
        dom.style.setProperty('will-change', 'transform');
        this.bdbox.appendChild(dom);
        const data = {
            width: px2vw(this.width),
            height: px2vw(this.height),
            src: Config.Resources.pacmanApng
        };
        dom.innerHTML = tpl(data);
        this.dom = dom;
    }
    /**
     * lottie
     * @param {boolean} bol 
     */
    mirror(bol) {
        bol
            ? this.dom.classList.add('mirror')
            : this.dom.classList.remove('mirror');
        return this;
    }
    initRole() {
    }
    /**
     * dom
     * @param {number} x axis-x
     * @param {number} y axis-y
     */
    syncPosition(x, y) {
        this.dom.style.transform = `translate3d(${px2vw(x / Config.AppConfig.renderSize.width * 750)},${px2vw(y / Config.AppConfig.renderSize.width * 750)}, 0)`;
        this._x = x;
        this._y = y;
        return this;
    }
    /**
     * 
     */
    move() {
    }
    turnRight() {
        this.mirror(true);
    }
    turnLeft() {
        this.mirror(false);
    }
    /**
     * 
     */
    eat() {
    }
    pause() {
    }
    resume() {
    }
    // lottie
    resetLottie() {
    }
    // 
    destroy() {
        this.bdbox && this.bdbox.removeChild && this.bdbox.removeChild(this.dom);
    }
}
