import './index.css';
import Config from '../../config';
import { isDOM, px2vw } from '../../libs/utils';
import { delay } from '../../libs/utils';
const tpl = (data) => `
  <div class="treasure-box" style="width:${data.width};height:${data.height};">
    <div id="treasure-box-img-scale">
      <div id="treasure-box-img-rotate">
        <img id="treasure-box-img-opacity" class="treasure-box-img" src=${data.src}></img>
      </div>
    </div>
  </div>
`;
export default class TreasureBoxUI {
    get position() {
        return { x: this._x, y: this._y };
    }
    constructor(config) {
        // pngwidthheight
        const { lottieData } = config;
        const { width, height } = lottieData.data;
        // console.log('>>>>> treasure', width, height);
        this.width = width || 300;
        this.height = height || 300;
        // 
        this._x = 0;
        this._y = 0;
        // 
        this.bdbox = isDOM(config.bdbox)
            ? config.bdbox
            : document.querySelector(config.bdbox) || document.body;
        this.config = config;
        this.initDom();
        // this.initRole();
    }
    /**
     * dom
     */
    initDom() {
        const dom = document.createElement('div');
        dom.id = 'TreasureBoxLottie';
        dom.style.setProperty('will-change', 'transform');
        this.bdbox.appendChild(dom);
        const data = {
            width: px2vw(this.width),
            height: px2vw(this.height),
            src: Config.Resources.treasureBoxApng
        };
        dom.innerHTML = tpl(data);
        this.dom = dom;
        // 
        this.imgScaleDom = document.getElementById('treasure-box-img-scale');
        this.imgRotateDom = document.getElementById('treasure-box-img-rotate');
        this.imgOpacityDom = document.getElementById('treasure-box-img-opacity');
    }
    initRole() {
    }
    /**
     * dom
     * @param {number} x axis-x
     * @param {number} y axis-y
     */
    syncPosition(x, y) {
        const { dpi, navBarHeight } = Config.AppConfig;
        this.dom.style.transform = `translate3d(${px2vw(dpi * x)},${px2vw(dpi * y)}, 0)`;
        this._x = x;
        this._y = y;
        return this;
    }
    /**
     * 
     *
     */
    play() {
        this.dom.style.display = 'block';
    }
    /**
     * 
     */
    playFlashing() {
        this.imgScaleDom && this.imgScaleDom.classList.add('treasure-box-img--scale');
        this.imgRotateDom && this.imgRotateDom.classList.add('treasure-box-img--rotate');
        this.imgOpacityDom && this.imgOpacityDom.classList.add('treasure-box-img--opacity');
        // 
        delay(1000).then(() => {
            this.dom.style.display = 'none';
        });
    }
    pause() {
    }
    resume() {
        // this.dom.style.display = 'none';
        this.imgScaleDom && this.imgScaleDom.classList.remove('treasure-box-img--scale');
        this.imgRotateDom && this.imgRotateDom.classList.remove('treasure-box-img--rotate');
        this.imgOpacityDom && this.imgOpacityDom.classList.remove('treasure-box-img--opacity');
    }
    // lottie
    resetLottie() {
    }
    // 
    destroy() {
        this.bdbox && this.bdbox.removeChild && this.bdbox.removeChild(this.dom);
    }
}
