___CSS_LOADER_EXPORT___.push([module.id, `#BeanRainCountDown {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

#BeanRainCountDown .game-count-down {
  position: absolute;
  top: -17.33333vw;
  left: 0;
  right: 0;
  bottom: 0;
}

#BeanRainCountDown.hidden {
  display: none;
}

#BeanRainCountDown .activity-tip {
  background: none;
  background-size: 100% 100%;
  width: 70.66667vw;
  height: 22.66667vw;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 5.33333vw;
  text-align: center;
}

#BeanRainCountDown .activity-tip em {
  background: none;
  background-size: 100% 100%;
  width: 24vw;
  height: 25.33333vw;
  display: inline-block;
}

#BeanRainCountDown.animate .activity-tip em {
  animation: move 0.9s ease-in-out 0s infinite alternate forwards;
}

@keyframes move {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(100%);
  }
}
