import Tiny from 'tiny';
;
import Config from '../config';
class Loader {
    constructor(resource) {
        this.event = new Tiny.EventEmitter();
        this.resource = resource;
        this.loader = new Tiny.loaders.Loader();
        this.init();
        this.bind();
        return this;
    }
    init() {
        const { Resources } = Config;
        // 
        const dropListResources = {};
        for (const i in this.resource) {
            const iconKey = this.resource[i].rewardType + this.resource[i].id;
            dropListResources[iconKey] = this.resource[i].icon;
        }
        // , 
        const resourcesCombined = Object.assign({}, Resources, dropListResources);
        for (const j in resourcesCombined) {
            if (!(j in this.loader.resources)) {
                this.loader.add(j, resourcesCombined[j], { crossOrigin: true });
            }
        }
        return this;
    }
    bind() {
        const instance = this;
        this.loader.on('complete', (_, resource) => {
            instance.event.emit('BeanRain:Loaded', {
                resource,
                mapping: this.resource,
            });
        });
        this.loader.on('error', (error) => {
            instance.event.emit('BeanRain:LoadError', {
                error
            });
        });
        return this;
    }
    load() {
        this.loader.load();
    }
}
export default Loader;
