import './index.css';
import lottie from 'lottie-web';
import Config from '../../config';
import { isDOM, px2vw } from '../../libs/utils';
const IS_ALIPAY = false; // 
const PacmanSegments = {
    Move: [1, 6],
};
const tpl = (data) => `<div class="pacman-box" style="width:${data.width};height:${data.height};"></div>`;
export default class PacmanLottie {
    get position() {
        return { x: this._x, y: this._y };
    }
    constructor(config) {
        const { lottieData } = config;
        const { w, h } = lottieData.data;
        this.width = w;
        this.height = h;
        // 
        this._x = 0;
        this._y = 0;
        // 
        this.bdbox = isDOM(config.bdbox)
            ? config.bdbox
            : document.querySelector(config.bdbox) || document.body;
        this.config = config;
        this.initDom();
        this.initRole();
    }
    /**
     * dom
     */
    initDom() {
        const dom = document.createElement('div');
        dom.id = 'PacmanLottie';
        dom.style.setProperty('will-change', 'transform');
        this.bdbox.appendChild(dom);
        const data = {
            width: px2vw(this.width),
            height: px2vw(this.height),
        };
        dom.innerHTML = tpl(data);
        this.dom = dom;
    }
    /**
     * lottie
     * @param {boolean} bol 
     */
    mirror(bol) {
        bol
            ? this.dom.classList.add('mirror')
            : this.dom.classList.remove('mirror');
        return this;
    }
    initRole() {
        const { lottieData } = this.config;
        this.lottie = lottie.loadAnimation({
            container: this.dom.children[0],
            renderer: 'svg',
            loop: true,
            autoplay: false,
            animationData: lottieData.data,
        });
    }
    bind() {
        // 
        this.lottie.addEventListener('DOMLoaded', () => {
            this.lottie.playSegments(PacmanSegments.Move, true);
        });
    }
    /**
     * dom
     * @param {number} x axis-x
     * @param {number} y axis-y
     */
    syncPosition(x, y) {
        const { dpi, navBarHeight } = Config.AppConfig;
        if (!IS_ALIPAY) {
            // this.dom.setPerperty(transform,xxx)
            this.dom.style.transform = `translate3d(${px2vw(dpi * x)},${px2vw(dpi * (y))}, 0)`;
            this.dom.style.transform = `translate3d(${px2vw(dpi * x)},${px2vw(dpi * y)}, 0)`;
        }
        else {
            this.dom.style.transform = `translate3d(${px2vw(dpi * x)},${px2vw(dpi * y)}, 0)`;
        }
        this._x = x;
        this._y = y;
        // this.dom.style.transform = `translate3d(${px2vw(
        //   x - this.width / dpi / 2
        // )},${px2vw(y - this.height / dpi / 2)}, 0)`;
        return this;
    }
    /**
     * 
     */
    move() {
        this.resetLottie();
        this.lottie.playSegments(PacmanSegments.Move, true);
    }
    turnRight() {
        this.mirror(true);
    }
    turnLeft() {
        this.mirror(false);
    }
    /**
     * 
     */
    eat() {
        // 
    }
    pause() {
        this.lottie.stop();
    }
    resume() {
        this.lottie.play();
    }
    // lottie
    resetLottie() {
        this.lottie.autoplay = false;
        this.lottie.loop = true;
        return this;
    }
    // 
    destroy() {
        this.lottie.destroy();
        this.bdbox && this.bdbox.removeChild && this.bdbox.removeChild(this.dom);
    }
}
