import './index.css';
import lottie from 'lottie-web';
import Config from '../../config';
import { isDOM, px2vw } from '../../libs/utils';
const IS_ALIPAY = false; // 
const Segments = {
    fly: [0, 8],
    flashing: [8, 16],
};
const tpl = (data) => `<div class="treasure-box" style="width:${data.width};height:${data.height};"></div>`;
export default class TreasureBoxLottie {
    get position() {
        return { x: this._x, y: this._y };
    }
    constructor(config) {
        const { lottieData } = config;
        const { w, h } = lottieData.data;
        this.width = w;
        this.height = h;
        // 
        this._x = 0;
        this._y = 0;
        // 
        this.bdbox = isDOM(config.bdbox)
            ? config.bdbox
            : document.querySelector(config.bdbox) || document.body;
        this.config = config;
        this.initDom();
        this.initRole();
    }
    /**
     * dom
     */
    initDom() {
        const dom = document.createElement('div');
        dom.id = 'TreasureBoxLottie';
        dom.style.setProperty('will-change', 'transform');
        this.bdbox.appendChild(dom);
        const data = {
            width: px2vw(this.width),
            height: px2vw(this.height),
        };
        dom.innerHTML = tpl(data);
        this.dom = dom;
    }
    initRole() {
        const { lottieData } = this.config;
        // lottie
        this.lottie = lottie.loadAnimation({
            container: this.dom.children[0],
            renderer: 'svg',
            loop: false,
            autoplay: false,
            animationData: lottieData.data,
        });
    }
    /**
     * dom
     * @param {number} x axis-x
     * @param {number} y axis-y
     */
    syncPosition(x, y) {
        const { dpi, navBarHeight } = Config.AppConfig;
        if (!IS_ALIPAY) {
            this.dom.style.transform = `translate3d(${px2vw(dpi * x)},${px2vw(dpi * (y))}, 0)`;
            this.dom.style.transform = `translate3d(${px2vw(dpi * x)},${px2vw(dpi * y)}, 0)`;
        }
        else {
            this.dom.style.transform = `translate3d(${px2vw(dpi * x)},${px2vw(dpi * y)}, 0)`;
        }
        this._x = x;
        this._y = y;
        return this;
    }
    /**
     * 
     *
     */
    play() {
        this.lottie.loop = true;
        this.lottie.playSegments(Segments.fly, true);
        this.dom.style.display = 'block';
    }
    /**
     * 
     */
    playFlashing() {
        this.resetLottie();
        this.lottie.playSegments(Segments.flashing, true);
        // 
        this.lottie.onComplete = () => {
            this.dom.style.display = 'none';
            // this.destroy();
        };
    }
    pause() {
        this.lottie.stop();
    }
    resume() {
        this.lottie.play();
    }
    // lottie
    resetLottie() {
        this.lottie.autoplay = false;
        this.lottie.loop = false;
        return this;
    }
    // 
    destroy() {
        this.lottie.destroy();
        this.bdbox && this.bdbox.removeChild && this.bdbox.removeChild(this.dom);
    }
}
