import './index.css';
import { isDOM, TreasureBoxShowTime } from '../../libs/utils';
import Tiny from 'tiny';
;
import Config from '../../config';
import getHeaderHeight from '../../getHeaderHeight';
const tpl = (data) => `
  <div class="ui-game-change">
    
  <div class="game-change-value">${data.gameChance}</div>
  </div>
  <div class="ui-timer">
    
    <div class="time-value">${data.time}s</div>
  </div>
  <div class="ui-score">
    
    <div class="score-value">0</div>
  </div>
`;
export default class GameUI {
    constructor(config) {
        this.config = config;
        this.gameChance = config.gameChance;
        this.hasTreasureBox = config.hasTreasureBox;
        this.onCountDownComplete = config.onCountDownComplete || function () { };
        this.onShowTreasureBox = config.onShowTreasureBox || function () { };
        this.bdbox = isDOM(config.bdbox)
            ? config.bdbox
            : document.querySelector(config.bdbox) || document.body;
        this.initDom();
        this.initCountDown();
    }
    /**
     *  UI dom
     */
    initDom() {
        const { gameTime } = Config.GameConfig;
        const dom = document.createElement('div');
        dom.id = 'BeanRainInfo';
        dom.classList.add('hidden');
        this.bdbox.appendChild(dom);
        getHeaderHeight().then(h => {
            dom.style.setProperty('transform', `translateY(${h.navBarHeight}px)`);
        });
        dom.innerHTML = tpl({
            time: gameTime,
            gameChance: this.gameChance,
        });
        this.dom = dom;
        this.timeDom = this.bdbox.querySelector('.time-value');
        this.scoreDom = this.bdbox.querySelector('.score-value');
        this.gameChanceDom = this.bdbox.querySelector('.game-change-value');
    }
    /**
     * 
     */
    initCountDown() {
        const { gameTime } = Config.GameConfig;
        this.timeCountDown = new Tiny.ticker.CountDown({
            duration: 1e3,
            times: gameTime,
        });
        this.bindEvent();
        // 
        this.gameChanceDom.innerHTML = `${this.gameChance - 1}`;
    }
    bindEvent() {
        const { gameTime, treasureBoxCycle } = Config.GameConfig;
        // 
        const treasureBoxShowTime = TreasureBoxShowTime(gameTime, treasureBoxCycle);
        // 
        this._finish = false;
        this.timeCountDown.on('update', () => {
            const remainTime = gameTime - this.timeCountDown.count;
            // 
            if (this.hasTreasureBox && this.timeCountDown.count === treasureBoxShowTime) {
                this.onShowTreasureBox();
            }
            this.timeDom.innerHTML = `${remainTime}s`;
        });
        this.timeCountDown.on('complete', () => {
            // fix: complete
            if (!this._finish) {
                this._finish = true;
                this.onCountDownComplete();
            }
        });
    }
    // 
    destroy() {
        this.bdbox && this.bdbox.removeChild && this.bdbox.removeChild(this.dom);
        // 
        this.timeCountDown.ticker.destroy();
        this.timeCountDown = null;
    }
    startCount() {
        this.dom.classList.remove('hidden');
        this.timeCountDown.start();
    }
    hide() {
        this.dom.classList.add('hidden');
        return this;
    }
    setScore(score) {
        this.scoreDom.innerHTML = score;
    }
    /**
     * 
     */
    reset() {
        const { gameTime } = Config.GameConfig;
        // 
        if (this.timeCountDown) {
            this.timeCountDown.destroy();
            this.timeCountDown = null;
        }
        this.timeDom.innerHTML = `${gameTime}s`;
        this.scoreDom.innerHTML = 0;
        return this;
    }
    restart() {
        this.reset();
        this.initCountDown();
        this.startCount();
        return this;
    }
    pause() {
        this.timeCountDown && this.timeCountDown.ticker && this.timeCountDown.pause();
    }
    resume() {
        this.timeCountDown && this.timeCountDown.start();
    }
    update(config) {
        this.gameChance = config.gameChance;
        this.hasTreasureBox = config.hasTreasureBox;
    }
}
