let Config = {};
const dpi = 2;
const AppConfig = {
    showFPS: true,
    debug: true,
    dpi,
    fps: 60,
    renderSize: {
        width: window.innerWidth * dpi,
        height: window.outerHeight * dpi,
    },
    referWidth: window.innerWidth,
    renderOptions: {
        transparent: true, // canvas 
    },
    navTriggerHeight: 0,
};
// 
Config.AppConfig = AppConfig;
// schema  
const parseSchema = (schema) => {
    const { beanImage, pacmanLottie, popupBackgroundImage, effectLottie, maxEffectAmount, gameTime, beanCycle, beanSizeRange, showEffectRate, countDownLottie, successPopupBg, gameTipBg, gameTipHand, autoShow, supplyInst, treasureBoxCycle, treasureBox, pacmanLottieToApng, treasureBoxLottieToApng, pacmanApng, treasureBoxApng, } = schema;
    // 
    const GameConfig = {
        maxEffectAmount,
        gameTime,
        beanCycle,
        beanSizeRange,
        showEffectRate,
        autoShow,
        supplyInst,
        treasureBoxCycle,
        pacmanLottieToApng,
        treasureBoxLottieToApng,
    };
    // 
    const Resources = {
        beanImage,
        pacmanLottie,
        // popupBackgroundImage,
        effectLottie,
        countDownLottie,
        successPopupBg,
        gameTipBg,
        gameTipHand,
        treasureBox,
        pacmanApng,
        treasureBoxApng,
    };
    // dom
    const StyleConfig = {
        popupBackgroundImage,
    };
    Config.GameConfig = GameConfig;
    Config.Resources = Resources;
    Config.StyleConfig = StyleConfig;
    console.log('>>>>> after parse', Config);
};
function setGameTime(time) {
    Config.GameConfig.gameTime = time;
}
// 
const LoadResource = {
    SUCCESS: 'SUCCESS',
    ERROR: 'ERROR',
};
Config.LoadResource = LoadResource;
// 
const RewardType = {
    PEA: 'pea',
    BOMB: 'bomb',
    TREASURE_BOX: 'treasureBox',
};
Config.RewardType = RewardType;
export { parseSchema, setGameTime };
export default Config;
